import React from 'react';
import Cell from './Cell';

function Row(props) {

  const columns = [ // columns of the table
    props.user.id,
    props.user.name,
    props.user.username,
    props.user.email,
    props.user.phone,
    props.user.address.city,
    props.user.website,
    props.user.company.name
  ];

  return (
    <tr>
      {columns.map((el,i) => <Cell
        key={i}
        i={i}             // –Ω–æ–º–µ—Ä –ø–µ—Ä–µ–¥–∞–≤–∞–µ–º–æ–π –∫–æ–ª–æ–Ω–∫–∏
        data={el}         // —á—Ç–æ –Ω–∞–ø–∏—Å–∞—Ç—å –≤ –∫–æ–ª–æ–Ω–∫–µ
        user={props.user} // –æ–±—ä–µ–∫—Ç user
        onUserSave={props.onUserSave}
        isAnotherCellEdited={props.isAnotherCellEdited}
        setIsAnotherCellEdited={props.setIsAnotherCellEdited}
      />)}
    </tr>
  );
};

export default Row;


